<template>
  <el-form label-width="120px" :model="config" :rules="rules" :inline="true" ref="configForm">
    <el-row>
      <el-col :span="5" :offset="1">
        <el-form-item prop="type">
          <el-radio-group  v-model="config.type" @change="change">
            <el-radio :label="1" style="width:110px">单一板块配费</el-radio>
            <el-radio :label="2" style="width:110px">单一来源配费</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="业务板块:" prop="bizSector" v-show="bizShow">
          <el-select v-model="config.bizSector">
            <el-option
              v-for="item in sectorList"
              :key="item.code"
              :label="item.name"
              :value="item.code">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="6">
        <el-form-item label="代理人名称:" prop="agent">
          <el-input v-model="config.agent"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="协议号:" prop="protocolNo">
          <el-input v-model="config.protocolNo"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="归属业务员:" prop="ownSalesMan">
          <el-input v-model="config.ownSalesMan"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="是否协议收费:" prop="isCharge">
          <el-select v-model="config.isCharge">
            <el-option label="是" value="y"></el-option>
            <el-option label="否" value="n"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="6">
        <el-form-item label="是否包含下级:" prop="isIncludeSub">
          <el-select v-model="config.isIncludeSub">
            <el-option label="是" value="y"></el-option>
            <el-option label="否" value="n"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="业务渠道:" prop="channel">
          <el-select v-model="config.channel" placeholder="请选择业务渠道" clearable>
            <el-option v-for="item in configFormData.channelList" :key="item.code" :value="item.code" :label="item.name"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="6">
        <el-form-item label="业务来源:" prop="source">
          <el-select v-model="config.source" placeholder="请选择业务来源" clearable>
            <el-option v-for="item in configFormData.sourceList" :key="item.code" :value="item.code" :label="item.name"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-form-item label="申报状态:" prop="declareFlag">
          <el-checkbox-group v-model="config.declareFlag">
            <el-checkbox label="1" name="declareFlag">未申报</el-checkbox>
            <el-checkbox label="2" name="declareFlag">待审批</el-checkbox>
            <el-checkbox label="3" name="declareFlag">已审批</el-checkbox>
            <el-checkbox label="4" name="declareFlag">待修改</el-checkbox>
            <el-checkbox label="5" name="declareFlag">需要重新申报</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'QueryForm',
  props: {
    config: {
      type: Object,
      required: true
    },
    rules: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      bizShow: false,
      sectorList: [
        {code: '1', name: '家用-新车'},
        {code: '2', name: '家用-旧车'},
        {code: '3', name: '企业非营业客车'},
        {code: '4', name: '企业非营业货车'},
        {code: '5', name: '机关非营业客车'},
        {code: '6', name: '机关非营业货车'},
        {code: '7', name: '机关非营业特种车'},
        {code: '8', name: '机动非营业客车'},
        {code: '9', name: '营业出租客车'},
        {code: '10', name: '营业出租货车'},
        {code: '11', name: '营业城市公交'},
        {code: '12', name: '营业旅游客车'},
        {code: '13', name: '营业公路货车'},
        {code: '14', name: '营业公路挂车'}
      ]
    }
  },
  created () {
    this.$store.dispatch('initConfigFormData')
  },
  computed: {
    ...mapState({
      configFormData: state => state.agency.configFormData
    })
  },
  methods: {
    reset () {
      this.config.declareFlag = []
      this.$refs['configForm'].resetFields()
    },
    change (val) {
      if (val === 1) {
        this.bizShow = true
      } else {
        this.bizShow = false
      }
      this.$emit('change')
    }
  }
}
</script>

<style scoped>

</style>



